/* MainPage.css */

/* Main container for the page */
.main-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-color: #f0f0f0;
    height: 100vh;
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
}


.main-page {
  padding: 20px;
}

.main-title {
  color: #001f3f; 
  font-size: 24px;
  margin-bottom: 20px;
}

.button-container {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.big-button {
  background-color: #FFD700;
  color: #3D2B1F;
  padding: 10px 20px;
  font-size: 16px;
  margin: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
}


.big-button:hover {
  background-color: #FFA500; 
}